@mixin fb($w, $py:20px, $px:20px){
    width: $w;
    max-width: $w;
    flex-basis: $w;
    padding: $py $px;
}

@mixin flexcenter($a:flex-start, $j:flex-start){
    display: flex;
    align-items: $a;
    justify-content: $j;
}

@mixin img($w, $h){
    object-fit: cover;
    width: $w;
    height: $h;
    object-position: center;
}
